:root {
  --primary-accent-color: #fd5103;
  --secondary-accent-color: #fe7031;
  --primary-text-color: #202025;
  --primary-black-color: #000;
  --secondary-text-color: #5f6775;
  --primary-white-color: #f5f5f5;
  --secondary-white-color: #fff;
  --light-on-dark: #fcfcfc;
  --transition-dur-and-func: 250ms cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.backdrop {
  z-index: 100;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, .2);
  flex-direction: column;
  justify-content: start;
  align-items: center;
  padding-top: 20px;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.button {
  letter-spacing: .02em;
  border-radius: 4px;
  font-family: Roboto;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.71;
}

.add-remove-btn {
  width: 110px;
  border: 1px solid var(--primary-accent-color);
  color: var(--primary-accent-color);
  transition: background-color var(--transition-dur-and-func);
  background-color: rgba(0, 0, 0, 0);
  justify-content: center;
  align-items: center;
  gap: 8px;
  padding-top: 6px;
  padding-bottom: 6px;
  display: flex;
}

.add-remove-btn:hover {
  background-color: var(--primary-white-color);
}

.add-remove-btn:active {
