.slider {
  cursor: pointer;
  background-color: var(--light-on-dark);
  border: 1px solid var(--primary-accent-color);
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 16px;
  width: 16px;
  background-color: var(--primary-accent-color);
  transition: all .4s;
  position: absolute;
  bottom: 1px;
  left: 2px;
}

input:checked + .slider {
  background-color: var(--primary-accent-color);
  border: 1px solid var(--light-on-dark);
}

input:focus + .slider {
  box-shadow: 0 0 1px var(--primary-accent-color);
}

input:checked + .slider:before {
  background-color: var(--light-on-dark);
  transform: translateX(18px);
}

